body {
    background-color: #2e2e31;
    margin: 0;
  }
  
  a {
    color: #fffbf1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    font-size: 80px;
  }
  
  a:before,
  a:after {
    content: "";
    padding: 0.9em 0.4em;
  
    position: absolute;
    left: 50%;
    width: 100%;
    top: 50%;
    display: block;
    border: 15px solid #50c9c3;
    border-radius: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    animation: 4s infinite alternate ease-in-out wobble;
  }
  
  a:before {
    border-color: #50c9c3 #50c9c3 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
    z-index: -1;
  }
  
  a:after {
    border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #50c9c3 #50c9c3;
    box-shadow: 25px 25px 25px rgba(46, 46, 49, 0.8);
  }
  
  .box {
    background: #f0ead6;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    width: 150px;
    border-radius: 20%;
    animation: 8s infinite alternate ease-in-out spin;
  }
  
  .innerBox {
    background: #2e2e31;
    height: 120px;
    width: 120px;
    border-radius: 20%;
  }
  
  @keyframes wobble {
    0% {
      transform: translateX(-50%) translateY(-50%) rotate(0deg);
    }
    100% {
      transform: translateX(-50%) translateY(-50%) rotate(720deg);
    }
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(720deg);
    }
  }
  